
























































import { Component, Vue } from "vue-property-decorator";
import { loguxMixin } from "@logux/vuex";
import { nanoid } from "nanoid";
import { Todo, add, remove, toggle } from "../../types/todos";

import Base from "./components/base";
import Login from "./components/Login.vue";
import TodoItem from "./components/TodoItem.vue";

@Component({
  components: { Login, TodoItem },
  mixins: [loguxMixin]
})
export default class App extends Base {
  get logged(): boolean {
    return this.$store.state.auth.status == "logged";
  }

  text: string = "";
  visibility: "all" | "active" | "completed" = "all";

  get channels() {
    const userId = this.$store.state.auth.userId;
    return this.logged ? [`todos/${userId}`] : [];
  }

  get all(): Todo[] {
    return this.$store.state.todo.list;
  }

  get active() {
    return this.all.filter(x => !x.done);
  }

  get completed() {
    return this.all.filter(x => x.done);
  }

  get allDone() {
    return this.active.length == 0;
  }

  get list() {
    switch (this.visibility) {
      case "all":
        return this.all;
      case "active":
        return this.active;
      case "completed":
        return this.completed;
      default:
        return [];
    }
  }

  get remaining() {
    return this.active.length;
  }

  get remainingText() {
    return this.remaining === 1 ? "item" : "items";
  }

  removeCompleted() {
    this.completed.forEach(x =>
      this.commitSync(
        remove({
          id: x.id
        })
      )
    );
  }

  toggleAll(done: boolean) {
    this.all
      .filter(x => x.done != done)
      .forEach(x =>
        this.commitSync(
          toggle({
            id: x.id,
            done
          })
        )
      );
  }

  append() {
    this.commitSync(
      add({
        id: nanoid(),
        text: this.text,
        done: false
      })
    );
    this.text = "";
  }
}
